.newsItem {
    width: 90%;
    margin: 1em auto 5em auto;
}

.newsSender {
    font-size: 21px;
    font-weight: 500;
    line-height: 26px;
}

.newsSender img {
    vertical-align: middle;
    margin-right: .5em;
    border-radius: 10px;
}

.newsTitle {
    font-size: 20px;
    font-weight: 700;
}

.newsContent {
    font-size: 16px;
    margin: .6em 0em 1em 0em;
}

.newsImg {
    overflow: hidden;
}

.newsImg img {
    width: 100%;
    border-radius: 10px;
}

.newsLikes {
    font-size: 25px;
    float: right;
}

.newsLikes span {
    vertical-align: super;
}

.newsLikes img {
    width: 40px;
}
